.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 18px;
}

.nav-links li {
  margin: 1rem;
}

.nav-links a {
  border: 1px solid transparent;
  border-radius: 20px;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  /* background: #ffb964; */
  border-color: transparent;
  color: #fa7453;
  transition: all 0.3s;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #fa7453;
  border-radius: 20px;
  color: #292929;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
  transition: all 0.3s;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #fa7453;
  color: white;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: black;
    text-decoration: none;
  }

  .nav-links button {
    background: #fa7453;
    color: white;
  }

  .nav-links button:hover,
  .nav-links button:active {
    background: white;
    color: #292929;
  }
}
